.holescontainer {
    display: flex;
    overflow-x: scroll;
    white-space: nowrap;
}
.holecontainer {
    min-width: 4em;
    max-width: 4em;
    text-align: center;
}

.holecell {
    border: 1px solid black;
    min-height: 4em;
    max-height: 4em;
}

.holeinput {
    /*display: block;*/
    text-align: center;
    height: 4em;
    line-height: 100%;
    max-width: 4em;
}

.cellSubmit {
    /*Hide those submit buttons! I dont want em!*/
    visibility: hidden;
    position: absolute;
    margin-left: 100%;
}

img {
    max-width: 100%;
    max-height: 100%;
    display: block;
}

.courseListContainer {
    display: flex;
}

.courseListImageContainer {
    max-width: 15em;
    min-width: 15em;
    padding-right: 0.67em;
}

.coursePictureGallery {
    overflow-x: scroll;
    white-space: nowrap;
}

.coursePictureContainer {
    display: inline-block;
    vertical-align: middle;  
}

.coursePictureContainer > img {
    min-height:15em;
    max-height: 15em;
}